<template>
  <div style='width:100%;height:100%;position:relative' onbeforeunload="return unloader()">
    <div class="row" v-if="ready_permission">
      <div class="col-12 flex" style='padding-left:0;padding-right:0'>
        <div v-if="currentform=='General Request'" :class="card_cls"> <!--general-->
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form1"  @submit.prevent="handleSubmit('form1')" >
        <div class="card" style='height:auto'>
          <div class="card-header">
            <strong>General Request Form</strong>
          </div>            
          <div class="card-body card-block">
            <div class="row form-group">
                <div class="col-3"><label for="Description" class=" form-control-label">Request Form</label></div>  <!--cause fresh-->
                <div class="col-9">
                  <div style="display:flex;flex-direction:row">
                      <div>
                        <input type="radio" value="General Request" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>General Request</label>
                      </div>
                      <!--
                      <div style="margin-left:2em">
                        <input type="radio" value="Locker Report" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Locker Report</label>
                      </div>
                      <div style="margin-left:2em">
                        <input type="radio" value="Hall Support" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Hall Support</label>
                      </div>
                      -->
                    </div>
                    <!--
                    <select name="Request_for" id="rf" class="form-control" v-model="form_display" @change="switchform">
                        <option v-for="sp in special">{{ sp }}</option>
                    </select>
                    -->
                </div>
            </div>
              <div class="row form-group">
                <div class="col-3"><label for="select" class=" form-control-label">Request For</label></div>
                <div class="col-9">
                  <!--<select name="Request_for" id="rf" class="form-control" v-model="req.req_for">
                        <option v-for="dp in reqdp">{{ dp }}</option>
                    </select>-->
                    <div style="display:flex;flex-direction:row">
                      <div>
                        <input type="radio" id="ir" value="IT & Resources" class="form-control-input" v-model="req.req_for">
                        <label for="one">IT & Resources</label>
                      </div>
                      <!--
                      <div style="margin-left:2em">
                        <input type="radio" id="aca" value="Academic" class="form-control-input" v-model="req.req_for">
                        <label for="two">Academic</label>
                      </div>
                      -->
                    </div>
                </div>
              </div>
              <div class="row form-group">
                <div class="col-3"><label for="Description" class=" form-control-label">Description</label></div>  <!--cause fresh-->
                <div class="col-9">
                    <textarea id ="Description" name="Description" rows="2" cols="80" placeholder="Enter description" maxlength="250" class="form-control" required v-model="req.descript"></textarea>
                </div>
              </div>
              <div class="row form-group">
                <div class="col-3"><label for="Description" class=" form-control-label">Extra Info (Image)</label></div>  <!--cause fresh-->
                <div class="col-9">
                    <input type="file" class="form-control-button" accept="image/jpeg" ref="multimedia_info" @change="addfile()" style="height:100%"/>
                </div>
              </div>
              <div class="row form-group">
                <div class="col-3"><label for="Location" class=" form-control-label">Location</label></div>
                <div class="col-9">
                    <input type="text" id="Location" name="Location" placeholder="Enter Location" maxlength="15"  class="form-control" v-model="req.location" required/>
                </div><!--cause fresh-->
              </div>
              <div class="row form-group">
                <div class="col-3"><label for="Due_date" class=" form-control-label">Due Date &amp; Time</label></div>
                <div class="col-9">
                    <input type='hidden' id='date_limit' name='date_limit' value=''/>
                    <!--
                    <input type="date" id="Due_date" name="Due_date" class="form-control" data-date-split-input="true" id="min_date" :min="min_date" required v-model="req.dd" />                 
                    -->
                    <!--
                    <VueCtkDateTimePicker :position="'absolute'" :inline="true" :noClearButton="true" :minDate="min_date" :autoClose="true" :buttonColor="'rgb(237,28,37)'" :color="'rgb(237,28,37)'" v-model="req.dd"  :onlyDate="true" :format="'YYYY-MM-DD'" :id="'DatePicker'" :label="'Select Date'" :formatted="'ll'"></VueCtkDateTimePicker>
                  -->
                  <VueCtkDateTimePicker 
                    :minuteInterval="5"
                    :noHeader="true"
                    :inline="true"
                    :noKeyboard="true"
                    :minDate="min_date" 
                    :buttonColor="'rgb(237,28,37)'" 
                    :color="'rgb(237,28,37)'"                     
                    :format="'YYYY-MM-DD HH:mm'" 
                    :id="'DateTimePicker'" 
                    :label="'Select Date'"
                    :noButtonNow="true"
                    :disabledWeekly="'0'"
                    v-model="req.ddt" 
                  >
                  </VueCtkDateTimePicker>
                </div>
              </div>                           
          </div>
        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
              <i class="fa fa-ban"></i> Reset
            </button>
        </div>
      </form>
    </div>
    <div v-if="currentform=='Locker Report'" :class="card_cls">
      <div class="card"> <!--locker-->
          <div class="card-header">
            <strong>Locker Issues Report Form</strong>
          </div>         
          <form id='locker_form' class="form-horizontal" data-vv-scope="form2"  @submit.prevent="handleSubmit_locker('form2')" > 
          <div class="card-body card-block" style='height:41.5vh!important'>  
           <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Request Form</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                  <div style="display:flex;flex-direction:row">
                      <div>
                        <input type="radio" value="General Request" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>General Request</label>
                      </div>
                      <div style="margin-left:2em">
                        <input type="radio" value="Locker Report" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Locker Report</label>
                      </div>
                      <div style="margin-left:2em">
                        <input type="radio" value="Hall Support" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Hall Support</label>
                      </div>
                    </div>
                    <!--
                    <select name="Request_for" id="rf" class="form-control" v-model="form_display" @change="switchform">
                        <option v-for="sp in special">{{ sp }}</option>
                    </select>
                    -->
                </div>
            </div>        
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Form</label></div>
                  <div class="col col-md-9" style='display:flex;flex-direction:row'>
                    <div class="col-4" style="padding-left:0">
                      <select name="Request_for" id="f" class="form-control select" v-model="locker.locker_form">
                            <option v-for="fl in formlevel">{{ fl }}</option>
                      </select>
                    </div>
                    <div class="col-4" style='text-align:right;padding-top:0.2rem'><label class=" form-control-label">Class</label></div>
                    <div class="col-4" style="padding-right:0">
                      <select name="Request_for" id="cls" class="form-control select" v-model="locker.locker_class">
                            <option v-for="fcls in formclass">{{ fcls }}</option>
                      </select>
                    </div>
                  </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Locker ID</label></div>
                  <div class="col-12 col-md-9">
                    <input type="number" id="locker_id" name="locker_id" class="form-control" v-model="locker.locker_id"/>
                  </div>
              </div>   
              <div class="row form-group">
                <div class="col col-md-3"><label class=" form-control-label">Description</label></div>
                  <div class="col-12 col-md-9">
                    <textarea id="locker_descp" name="locker_descp" rows="2" cols="80" placeholder="Enter Description" class="form-control" v-model="locker.locker_desc" required></textarea>
                  </div>
              </div>           
          </div>
          <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm">
              <i class="fa fa-ban"></i> Reset
            </button>
          </div>
          </form>
          </div>
        </div> <!--locker-->

     <div v-if="currentform=='Hall Support'" :class="card_cls">
     <div class="card" style='height:auto'><!--HALL-->
        <div class="card-header">
          <strong>Hall Support Request Form</strong>
        </div>             
        <form class="form-horizontal" @submit.prevent="handleSubmit_hall('form3')" data-vv-scope="form3">
          <div class="card-body card-block"> 
            <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Request Form</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                  <div style="display:flex;flex-direction:row">
                      <div>
                        <input type="radio" value="General Request" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>General Request</label>
                      </div>
                      <div style="margin-left:2em">
                        <input type="radio" value="Locker Report" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Locker Report</label>
                      </div>
                      <div style="margin-left:2em">
                        <input type="radio" value="Hall Support" class="form-control-input" v-model="form_display" @change="switchform">
                        <label>Hall Support</label>
                      </div>
                    </div>
                    <!--
                    <select name="Request_for" id="rf" class="form-control" v-model="form_display" @change="switchform">
                        <option v-for="sp in special">{{ sp }}</option>
                    </select>
                    -->
                </div>
            </div>        
              <div class="row form-group">
                  <div class="col col-md-3"><label class=" form-control-label">Event Title</label></div>
                  <div class="col-12 col-md-9">
                    <input type="text" name="hall_desc" class="form-control" placeholder="Enter Event Title" v-model="hall.etitle" required/>    
                  </div>             
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Application Form</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                  <div class="flex">
                    <div>
                      <label style="font-size:0.85em">Step1:</label>
                      <a href="src/RequestPortal/pdf_files/IR03_base_form.pdf" class="button" target="_blank" style="margin-right:3em"><u>Download IR03 Form</u></a>
                    </div>
                    <div>
                      <label style="font-size:0.85em">Step2: Upload IR03 Form</label>
                      <input type="file" class="form-control-button" accept=".pdf" ref="pdf_file" @change="addfile_pdf()" style="height:100%" required/>
                    </div>
                  </div>
                </div>
              </div> 
              <div class="row form-group">
                <div class="col col-md-3"><label for="nf-email" class=" form-control-label">Date &amp; Start time</label></div>
                <div class="col-12 col-md-9">
                  <VueCtkDateTimePicker 
                    :minuteInterval="5"
                    :noHeader="true"
                    :inline="true"
                    :noKeyboard="true"
                    :minDate="min_date" 
                    :buttonColor="'rgb(237,28,37)'" 
                    :color="'rgb(237,28,37)'"                     
                    :format="'YYYY-MM-DD HH:mm'" 
                    :id="'DateTimePicker'" 
                    :label="'Select Date'"
                    :noButtonNow="true"
                    v-model="hall.rdatetime" 
                  >
                  </VueCtkDateTimePicker>
                  <!--
                  <input type="date" name="hall_date" id="date_hall" :min="min_date" class="form-control" data-date-split-input="true"  required v-model="hall.rdate" />
                  -->
                </div>
                </div>
                <div class="row form-group">
                  <div class="col col-md-3"><label class=" form-control-label">End Time</label></div>
                  <div class="col-12 col-md-9">
                      <VueCtkDateTimePicker 
                      :minuteInterval="5"
                      :noHeader="true"
                      :inline="true" 
                      :noKeyboard="true"
                      :buttonColor="'rgb(237,28,37)'" 
                      :color="'rgb(237,28,37)'" 
                      v-model="hall.ed_time" 
                      :only-time="true" 
                      :format="'HH:mm'" 
                      :id="'TimePicker'" 
                      :label="'Select Time'" 
                      :formatted="'HH:mm'"
                    >
                    </VueCtkDateTimePicker>
                    <!--<input type="time" name="hall_st_time" value="00:00" class="form-control" v-model="hall.ed_time"/>-->
                  </div>
                </div>                                                               
          </div>
          <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm">
              <i class="fa fa-ban"></i> Reset
            </button>           
          </div>
        </form>
      </div>
      </div>
      <div v-show="false" v-if="permission>=2"><!--check form Start-->      
        <form id='general_req_form' enctype="multipart/form-data" class="form-horizontal" data-vv-scope="form4"  @submit.prevent="handlecheck('form4')" >
        <div class="card" style='height:auto'>
          <div class="card-header">
            <strong>Regular Request Form for IR Committee</strong>
          </div>            
          <div class="card-body card-block">
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Sub-Department</label></div>
                <div class="col-12 col-md-9">
                  <select name="Request_for" id="rf" class="form-control select" v-model="eq.subdept" @change="clear_item">
                        <option v-for="subdp in subreqdp">{{ subdp }}</option>
                    </select>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="select" class=" form-control-label">Item Category</label></div>
                <div class="col-12 col-md-9" v-if="eq.subdept=='ITSD'">
                  <select name="Request_for" id="rf" class="form-control select" v-model="eq.it_item">
                        <option v-for="itac in it_assets">{{ itac }}</option>
                    </select>
                </div>
                <div class="col-12 col-md-9" v-if="eq.subdept=='Resources'">
                  <select name="Request_for" id="rf" class="form-control select" v-model="eq.r_item">
                        <option v-for="rac in r_assets">{{ rac }}</option>
                  </select>
                </div>
              </div>
              <div class="row form-group" v-if="eq.r_item=='Others' || eq.it_item=='Others'" v-model="eq.item_other">
                <div class="col col-md-3"><label class=" form-control-label">Item Description</label></div>
                <div class="col-12 col-md-9">
                    <input type="text" id="Location" name="Location" placeholder="Enter Item Description" maxlength="150"  class="form-control" v-model="eq.item_desc" required/>
                </div><!--cause fresh-->
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Problem Description</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <textarea id ="Description" name="Description" rows="2" cols="80" placeholder="Enter description" maxlength="250" class="form-control" required v-model="eq.descript"></textarea>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Description" class=" form-control-label">Extra Info (Image)</label></div>  <!--cause fresh-->
                <div class="col-12 col-md-9">
                    <input type="file" class="form-control-button" accept="image/jpeg" capture="camera" ref="check_info" @change="addfile_check()" style="height:100%"/>
                </div>
              </div>
              <div class="row form-group">
                <div class="col col-md-3"><label for="Location" class=" form-control-label">Location</label></div>
                <div class="col-12 col-md-9">
                    <input type="text" id="Location" name="Location" placeholder="Enter Location" maxlength="15"  class="form-control" v-model="eq.location" required/>
                </div><!--cause fresh-->
              </div>                                  
          </div>
        <div class="card-footer">
            <button type="submit" class="btn btn-primary btn-sm">
              <i class="fa fa-paper-plane"></i> Submit
            </button>
            <button type="reset" class="btn btn-danger btn-sm" onclick="document.getElementById('general_req_form').reset()">
              <i class="fa fa-ban"></i> Reset
            </button>
        </div>
      </form> 
      </div>     
      <!--check form-->  
    </div>  
    </div>
  </div>
</template>


<script>
import axios from 'axios';
export default {
async created(){   
    await axios.get('../user_info.php') 
      .then(
            response=>{
                this.userid = response.data
            }
        );
      await axios.get('../permission.php')
              .then(
              response=>{
                  this.permission = response.data[0].portal_level;
                  if(this.permission>=2){
                    this.card_cls = 'col-12';
                  } else {
                    this.card_cls = 'col-12';
                  }
                  this.ready_permission = true;
              }
          );
  },
     mounted(){     
        window.onbeforeunload = function (e) {
            e = e || window.event;
            // 兼容IE8和Firefox 4之前的版本
            if (e) {
                e.returnValue = 'Changes you made may not be saved.';
            }
            // Chrome, Safari, Firefox 4+, Opera 12+ , IE 9+
            return 'Changes you made may not be saved.';
        }
         var tdy = new Date();
            var month_tdy = ('0' + (tdy.getMonth() + 1)).slice(-2);
            var date_tdy = ('0' + tdy.getDate()).slice(-2);
            var year_tdy = tdy.getFullYear();
            var formattedDate_tdy = year_tdy + '-' + month_tdy + '-' + date_tdy;
         
         var tmr = new Date();
         tmr.setDate(tmr.getDate() + 1);
            var month = ('0' + (tmr.getMonth() + 1)).slice(-2);
            var date = ('0' + tmr.getDate()).slice(-2);
            var year = tmr.getFullYear();
            var formattedDate = year + '-' + month + '-' + date; 
            this.min_date = formattedDate;
            this.req.dd = this.min_date;
            this.hall.rdate = this.min_date;
            this.req.ddt = this.min_date+' 12:00'
            this.hall.rdatetime = formattedDate+' 07:45';                
    
    },   
    destroyed() {
      window.onbeforeunload = null;
    },
    data(){          
        return{
            card_cls:'',
            req:{
                req_for:'IT & Resources',
                descript:'',
                location:'',
                dd:'',
                dt:'00:00',
                ddt:'',
            },  
            locker:{
              locker_form:'F1',
              locker_class:'A',
              locker_id:'1',
              locker_desc:'',
            },
            hall:{
              st_time:'07:45',
              ed_time:'12:00',
              rdate:'',
              etitle:'',
              rdatetime:'',
            },
            eq:{
                subdept:'ITSD',
                it_item:'',
                r_item:'',
                item_other:'',
                item_desc:'',
                location:'',
                descript:''

            },
            min_date:'',
            files:'',
            pdf:'',
            img_upload:'',
            submitted: false,        
            reqdp:['IT & Resources','Academic'],  
            formlevel:['F1','F2','F4','F5'], 
            //formlevel:['F1','F2','F3','F4','F5'],  
            formclass:['A','B','C','D','E'],
            userid:[],
            subreqdp:['Resources','ITSD'],
            it_assets:['IPAD Mount','Monitor','Computer','Peripherals','Amplifier','Matrix','Projector','Apple TV','Cabling','Visualizer','Others'],
            r_assets:['Teacher Desk','IPAD Desk','Chairs','Projector Screen','Projector','Student Desk','Others'],
            currentform:'General Request',
            special:['General Request','Locker Report','Hall Support'],
            form_display:'General Request',
            permission:0,
            ready_permission:false,
        }  
    },
    methods:{
       addfile(){
         var type = this.$refs.multimedia_info.files[0].type;
         if(type=='image/jpeg'){
           this.files =  this.$refs.multimedia_info.files[0];
         } else {
           this.$alert('Please upload image file with extension (.jpg / .jpeg)');
           this.$refs.multimedia_info.value = '';
         }        
       },
       addfile_check(){
         var type = this.$refs.check_info.files[0].type;
         if(type=='image/jpeg'){
           this.files =  this.$refs.check_info.files[0];
         } else {
           this.$alert('Please upload image file with extension (.jpg / .jpeg)');
           this.$refs.check_info.value = '';
         }  
       },
       handleSubmit(scope){  
              this.submitted = true;
              var formData = new FormData();
              var ddt = this.req.ddt.split(' ');
              this.req.dd = ddt[0];
              this.req.dt = ddt[1];
              formData.append('Requester', this.userid[0]);
              formData.append('Request_for',this.req.req_for);
              formData.append('Description',this.req.descript);
              formData.append('img', this.files);
              formData.append('Location',this.req.location);
              formData.append('Due_date',this.req.dd);
              formData.append('Due_time',this.req.dt);
              axios.post('RequestPortal/backend/insert_new_request.php',formData)
                .then(
                  (response)=>{ 
                    if(response.data=='OK'){
                        this.$alert("Successfully Submitted Form");
                        this.$router.push('PortalHome');
                      } else {
                        this.$alert(response.data);
                      }
                    }
                  )                        
       },
       handleSubmit_locker(scope){     
              this.submitted = true;
              this.$validator.validateAll(scope).then(valid => {
                 if (valid) {
                        var formData = new FormData();
                        formData.append('Requester', this.userid[0]);
                        formData.append('Form',this.locker.locker_form);
                        formData.append('Classroom',this.locker.locker_class);
                        formData.append('LockerID',this.locker.locker_id);
                        formData.append('Problem',this.locker.locker_desc);
                        axios.post('RequestPortal/backend/insert_locker.php',formData);
                        this.$alert("Successfully Submitted Form");
                        this.$router.push('PortalHome');
              }
            });
          },
      handleSubmit_hall(scope){     
              this.submitted = true;
              this.$validator.validateAll(scope).then(valid => {
                 if (valid) {
                        var formData = new FormData();
                        var ddt = this.req.ddt.split(' ');
                        this.hall.rdate = ddt[0];
                        this.hall.st_time = ddt[1];
                        formData.append('Requester', this.userid[0]);
                        formData.append('start_time',this.hall.st_time);
                        formData.append('end_time',this.hall.ed_time);
                        formData.append('date',this.hall.rdate);
                        formData.append('event',this.hall.etitle);
                        formData.append('pdf',this.pdf);
                        axios.post('RequestPortal/backend/insert_hall.php',formData);
                        this.$alert("Successfully Submitted Form");
                        this.$router.push('PortalHome');
              }
            });
          },
      handlecheck(scope){
        this.submitted = true;
        var formData = new FormData();
        formData.append('Requester', this.userid[0]);
        formData.append('Sub_dept',this.eq.subdept);
        formData.append('Item_category_it',this.eq.it_item);
        formData.append('Item_category_r',this.eq.r_item);
        formData.append('others',this.eq.item_desc);
        formData.append('problem',this.eq.descript);
        formData.append('img',this.files);
        formData.append('location',this.eq.location);
        axios.post('RequestPortal/backend/insert_check.php',formData);
        this.$router.push('PortalHome');
      },
      clear_item(){
        this.eq.it_item='';
        this.eq.r_item='';
        this.eq.item_desc='';
      },
      switchform(event){
          this.currentform=this.form_display;
      },
      addfile_pdf(event){
        var type = this.$refs.pdf_file.files[0].type;
         if(type=='application/pdf'){
           this.pdf =  this.$refs.pdf_file.files[0];
         } else {
           this.$alert('Please upload pdf file with extension (.pdf)');
           this.$refs.pdf_file.value = '';
         }
      }
    }
}
</script>


<style scoped>
  .input-group-addon {
    background-color: transparent;
    border-left: 0;
}
input[type=date]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    display: none;
}
.card{
  max-height:90vh!important;
}
</style>
<style>
.date-time-picker{
  z-index:100!important;
}
</style>


